@import url("https://fonts.googleapis.com/css2?family=Red+Hat+Display:wght@300&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Red Hat Display", sans-serif;
  font-style: inherit;
}

.button-contact {
  margin-top: 2rem;
  width: 30vh;
  height: 4rem;
  background-color: transparent;
  border-color: rgb(20, 219, 176);
  color: rgb(20, 219, 176);
  font-size: 1.5rem;
}

.button-contact:hover {
  cursor: pointer;
  background-color: rgb(20, 219, 176);
  color: #fff;
  opacity: 0.9;
  transition: linear;
}

.asidehome {
  margin: 20% 5%;  
  background-repeat: no-repeat;
  background-size: 100% 100%;
  height: 60vh;
  animation-duration: 20s;
  animation-name: banner;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

@keyframes banner {
  0%,
  65% {
    background-image: url("../Environment/ecommer.gif");
  }
  70%,
  80% {
    background-image: url("../Environment/Dulceria.gif");
  }
  80%,
  90% {
    background-image: url("../Environment/clima.gif");
  }
  90%,
  100% {
    background-image: url("../Environment/RickandMorty.gif");
  }
}

.button-aside {
  margin-top: 2rem;
  width: 30vh;
  height: 4rem;
  background-color: rgb(20, 219, 176);
  border-color: rgb(20, 219, 176);
  color: #000;
  font-size: 1.5rem;
}

.button-aside:hover {
  cursor: pointer;
  background-color: #000;
  color: #fff;
  opacity: 0.9;
  transition: linear;
}
